<template>
  <div>
    <el-row>
      <el-col>
        <el-table :data="issueVOs" style="width: 100%">
          <el-table-column label="题目ID" prop="issueID" v-if="show = false"></el-table-column>
          <el-table-column label="题目" prop="issueContent"></el-table-column>
          <el-table-column label="选项A" prop="optionA"></el-table-column>
          <el-table-column label="选项B" prop="optionB"></el-table-column>
          <el-table-column label="选项C" prop="optionC"></el-table-column>
          <el-table-column label="选项D" prop="optionD"></el-table-column>
          <el-table-column label="答案" prop="answer"></el-table-column>
          <el-table-column align="right">
            <template slot-scope="scope">
              <el-button size="small" type="primary" @click="add(scope.row)" round>
                <i class="el-icon-circle-plus-outline" />添加该题
              </el-button>
            </template>
          </el-table-column>
        </el-table>
      </el-col>
    </el-row>
    <!-- 设置分数模态框 -->
    <el-dialog title="设置该题分值" :visible.sync="dialogFormVisible">
      <el-form ref="point" :rules="rules" :model="paperIssue">
        <el-form-item label="该题分值为：" :label-width="formLabelWidth" prop="point">
          <el-input v-model="paperIssue.point" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="resetForm('point')">重 置</el-button>
        <el-button type="primary" @click="Post('point')">添 加</el-button>
      </div>
    </el-dialog>
    <el-row>
      <el-col>
        <el-pagination
          background
          @current-change="handleCurrentChange"
          :current-page.sync="currentPage"
          :page-size="5"
          layout="total, prev, pager, next"
          :total="total"
        ></el-pagination>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  data() {
    return {
      dialogFormVisible: false,
      currentPage: 1,
      total: 0,
      issueVOs: [],
      paperIssue: {
        issueID: 0,
        issueTypeID: sessionStorage.getItem("issueTypeID"),
        issueNum: 0,
        answer: "",
        point: 0
      },
      formLabelWidth: "120px",
      rules: {
        point: [{ required: true, message: "请输入题目分值", trigger: "blur" }]
      }
    };
  },
  methods: {
    add(row) {
      this.paperIssue.issueID = row.issueID;
      this.paperIssue.answer = row.answer;
      this.dialogFormVisible = true;
    },
    Post(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          var paperVO = JSON.parse(sessionStorage.getItem("paperVO"));
          this.paperIssue.issueNum = paperVO.paperIssues.length + 1; //题号
          paperVO.paperIssues.push(this.paperIssue); //把paperIssue对象添加进paperVO里的list集合
          var newpaperVO = JSON.stringify(paperVO);
          sessionStorage.setItem("paperVO", newpaperVO);
          console.log("最新问卷信息：" + newpaperVO);
          this.$message({
            showClose: true,
            message: "题目 + 1",
            type: "success"
          });
          this.dialogFormVisible = false;
        } else {
          this.$message({
            showClose: true,
            message: "请先输入题目分值！",
            type: "error"
          });
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    handleCurrentChange(current) {
      this.currentPage = current;
      var api = "http://localhost:8888/paper/single/" + current + "/" + 5;
      this.axios
        .get(api)
        .then(response => {
          console.log("分页响应对象：" + response);
          this.issueVOs = response.data.records;
          this.total = response.data.total;
        })
        .catch(error => {
          console.log(error);
        });
    },
    getData() {
      var issueTypeID = sessionStorage.getItem("issueTypeID");
      if (issueTypeID == 1) {
        var api = "http://localhost:8888/paper/single/" + 1 + "/" + 5;
        this.axios
          .get(api)
          .then(response => {
            console.log(response);
            this.issueVOs = response.data.records;
            this.total = response.data.total;
          })
          .catch(error => {
            console.log(error);
          });
      } else if (issueTypeID == 2) {
        var api = "http://localhost:8888/paper/many/" + 1 + "/" + 5;
        this.axios
          .get(api)
          .then(response => {
            console.log(response);
            this.issueVOs = response.data.records;
            this.total = response.data.total;
          })
          .catch(error => {
            console.log(error);
          });
      } else if (issueTypeID == 3) {
        var api = "http://localhost:8888/paper/judge/" + 1 + "/" + 5;
        this.axios
          .get(api)
          .then(response => {
            console.log(response);
            this.issueVOs = response.data.records;
            this.total = response.data.total;
          })
          .catch(error => {
            console.log(error);
          });
      } else {
        return null;
      }
    }
  },
  created() {
    this.getData();
  }
};
</script>

<style lang="scss" scoped>
</style>