<template>
  <div>
    <el-row type="flex" justify="center">
      <el-col :span="8">
        <el-input v-model="paperVO.paper.paperName" @blur="stringify" placeholder="请输入问卷名称"></el-input>
      </el-col>
      <el-col :span="4">
        <el-button size="small" type="success" @click="selectSingle">
          <i class="el-icon-circle-plus-outline" />添加单选题
        </el-button>
      </el-col>
      <el-col :span="4">
        <el-button size="small" type="success" @click="selectMany">
          <i class="el-icon-circle-plus-outline" />添加多选题
        </el-button>
      </el-col>
      <el-col :span="4">
        <el-button size="small" type="success" @click="selectJudge">
          <i class="el-icon-circle-plus-outline" />添加判断题
        </el-button>
      </el-col>
      <el-col :span="4">
        <el-button size="small" type="warning" @click="post">
          <i class="el-icon-circle-plus-outline" />发布问卷
        </el-button>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="24">
        <IssueList :key="timer"></IssueList>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import IssueList from "./IssueList";
import Qs from "qs";

export default {
  components: {
    IssueList
  },
  data() {
    return {
      timer: "",
      paperVO: {
        paper: {
          paperName: ''
        },
        paperIssues: []
      }
    };
  },
  methods: {
    post() {
      var paperVO = JSON.parse(sessionStorage.getItem("paperVO"));
      console.log("post问卷对象：" + sessionStorage.getItem("paperVO"));
      var api = "http://localhost:8888/paper/add/paper";
      this.axios
        .post(api, paperVO)
        .then(response => {
          console.log(response);
          if (response.status == 200) {
            this.$message({
              showClose: true,
              message: "发布成功",
              type: "success"
            });
            this.$router.push({
              name: 'paperList',
              params: { current: 1, size: 5 }
            });
          }
        })
        .catch(error => {
          console.log(error);
          this.$message({
            showClose: true,
            message: "操作失败，请联系维护人员",
            type: "error"
          });
        });
    },
    stringify() {
      var paperVO = JSON.stringify(this.paperVO);
      sessionStorage.setItem("paperVO", paperVO);
    },
    selectSingle() {
      sessionStorage.setItem("issueTypeID", 1);
      this.timer = new Date().getTime(); //刷新子组件
    },
    selectMany() {
      sessionStorage.setItem("issueTypeID", 2);
      this.timer = new Date().getTime(); //刷新子组件
    },
    selectJudge() {
      sessionStorage.setItem("issueTypeID", 3);
      this.timer = new Date().getTime(); //刷新子组件
    }
  }
};
</script>

<style lang="scss" scoped>
</style>